$font-primary: 'Roboto';
$font-secondary: 'Inter', sans-serif;

$main-text-color: #000000;
$second-text-color: #ffffff;
$third-text-color: #ff6b08;


$breakpoint-mobile: 480px;
$breakpoint-tablet: 768px;
$breakpoint-desktop: 1280px;

body {
  margin: 0 auto;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  @media screen and (min-width: 1280px) {
    max-width: 1280px;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}
a {
  color: #000000;
  &:link,
  &:hover {
    text-decoration: none;
  }
}

.is-hidden {
	pointer-events: none;
	visibility: hidden;
	opacity: 0;
	transform: scale(0);
}
.backdrop {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	justify-content: center;
	background: rgba(0, 0, 0, 0.2);
	transition-property: visibility, opacity, transform;
	transition-duration: 700ms;
	transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}